'use client'
import { useState } from "react";
import BasicTitle from "../BasicTitle/BasicTitle";

export default function PhotosBlock() {

       let videoId = 1;
        const videosCount = 14;
    
        const [data, setData] = useState(new Array(6).fill(null).map((_) => {
            const video = { name: `Название видео ${videoId}`, href: '', src: '' };
            videoId++;
            return video;
        }));
    
        const [loadingStatus, setLoadingStatus] = useState<boolean>(false);
    
        const getMoreVideos = () => {
            setLoadingStatus(true);
            setTimeout(
                () => {
    
                    const countMoreVideos = Math.min(videosCount - data.length, 6)
                    const moreVideos = new Array(countMoreVideos).fill(null).map((_) => {
                        const video = { name: `Название видео ${videoId}`, href: '', src: '' };
                        videoId++;
                        return video;
                    });
                    setData([...data, ...moreVideos]);
                    setLoadingStatus(false);
                }, 1000
            )
        }

    return (
        <div className="w-full flex flex-col justify-center items-center">
            <BasicTitle text="Фото" fontSize={30} fontWeight={500} />
            <div className="w-full flex flex-row justify-center flex-wrap gap-[30px]">
            </div>
        </div>
    )
}